			// 			console.log(this.screenHeight)
			this.isShow = !this.isShow;

			console.log(this.isShow);
		},
		getCharacterNum(e) {
			//  获取输入文字数量
			// console.log(e.detail.value)
			this.CharacterNum = e.detail.value.length;
		},
		abolishRetroaction() {
			//点击取消  取消反馈
			this.isShow = !this.isShow;
		},
