	data() {
		return {
			showMask: false,
			userName: '输入手机',
			extractSucceed: false, //  判断用户是否  用户是否需要提现
		};
	},
	methods: {
		getDrawingsBox() {
			//  点击弹出提现提 遮罩层
			// 			uni.hideTabBar({
			// 				animation: true
			// 			});

			this.showMask = !this.showMask;

			this.extractSucceed = false;
		},
		confirmDrawings() {
			// 点击确认提现 弹出 提交成功  提示
			this.extractSucceed = true;
		},
		cancelDrawings() {
			// 点击  取消  隐藏  遮罩层
			this.showMask = !this.showMask;
			// 			uni.showTabBar({
			// 				animation: true
			// 			})
		},
		confirmExtractSucceed() {
			//确定提现成功  隐藏遮罩层
			this.showMask = false;
		},
		makingACall() {
			//点击客服  拨打电话
			uni.makePhoneCall({
				phoneNumber: '114' //仅为示例
			});
		}
	},
	onShow() {
		if (uni.getStorageSync('userMobile')) {
			//到本地  取出用户昵称
			this.userName = uni.getStorageSync('userMobile');
		}

		// 隐藏遮罩层
		this.showMask = false;
	},
	onLoad() {
		// 		uni.showLoading({
		// 			title: '加载中'
		// 		});
		//
		// 		setTimeout(function() {
		// 			uni.hideLoading();
		// 		}, 500);
	}
};
